    font-size: 18px;
    transition-duration: 1s;
}
::-webkit-input-placeholder{
    color:#878787;
}
:-ms-input-placeholder{
    color:#878787;
}
::placeholder{
    color:#878787;
}
.emailInput:focus{
    border-bottom: 2px solid #878787;
    outline: none;
}
.nameInput:focus{
    border-bottom: 2px solid #878787;
    outline: none;
}
.messageInput:focus{
    border-bottom: 2px solid #878787;
    outline: none;
}
.emailInput{
    width: 300px;
    margin-left: 50px;
    margin-top: 10px;
    height: 50px;
    border: none;
    border-bottom: 2px solid #f0f0f0;
    letter-spacing: .6px;
    padding-left: 10px;
    font-size: 18px;
    transition-duration: 1s;
}
.messageInput{
    margin-top: 10px;
    margin-left:50px;
    height:150px;
