    font-size: 18px;
    transition-duration: 1s;
}

.sendButton{
    margin-top: 50px;
    height: 50px;
    width: 200px;
    margin-left: 500px;
    border-radius: 50px;
    background-color:#2b2b2b;
    color: white;
    border: none;
    transition-duration:.5s;
    outline: none;
    letter-spacing: .8px;
}
.sendButton:hover{
    background-color:#7682cc;
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
}
.sendButton:active{
    transition-duration: 0s;
    background-color: #6371ca;
}
.contactInfo{
    margin-bottom: 20px;
    font-weight: bold;
